nav 
	position: fixed
	height: 50px 
	background-color: #212121 
	z-index: 5
	width: 100%

header 
	position: relative 
	height: 50px


.hamburger
	display: none

.subheader 
	top: 50px
	height: 87px 
	background-color: rgba(0,0,0, .6) 
	position: absolute 
	width: 100% 
	&_logo 
		display: block 
		margin-top: 16px 
		max-width: 100% 
	&_official 
		margin-top: 6px 
		font-weight: 100 
		font-size: 13px 
		opacity: 0.5 
		color: #FFFFFF 
	&_call 
		margin-top: 19px 
		font-weight: 300 
		font-size: 12px 
		color: #FFFFFF 
	&_phone 
		margin-top: 8px 
		font-weight: 400 
		font-size: 22px 
		text-transform: uppercase 
		color: #FFFFFF 
	&_btn 
		margin-top: 19px 
		width: 172px 
		height: 46px 
		box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12) 
		border: none 
		border-radius: 4px 
		background: linear-gradient(0deg, $main_blue_color 0%, #4CE2FF 100%) 
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21) 
		color: #FFFFFF 
		font-size: 14px 
		font-weight: 400 
		text-transform: uppercase 
		text-align: center 
		+btn_hover


.col-md-2, .col-md-4 
	padding: 0 
h3 
	margin: 0 
.menu 
	display: flex 
	justify-content: space-between 
	padding: 13px 70px 
	margin: 0 
	list-style: none 
	&_link 
		font-weight: 300 
		font-size: 15px 
		color: #FFFFFF 
		&:hover 
			color: #FFFFFF 
	&_item 
		border-right: 2px dotted black 
		padding-right: 28px 
		&:last-child
			border-right: none
.promo 
	min-height: 800px 
	background: url(../img/uber_bg.png) center center/cover no-repeat 
	padding: 150px 0 135px 0 
	&_header 
		font-weight: 900 
		font-size: 60px 
		text-align: center 
		text-transform: uppercase 
		color: #FFFFFF 
		line-height: 60px 
		margin: 0 
		text-align: center 
	&_subheader 
		font-weight: 700 
		font-size: 36px 
		text-align: center 
		text-transform: uppercase 
		color: #FFFFFF 
		line-height: 36px 
		margin: 0 
		margin-top: 22px 
		text-align: center 
	&_descr 
		font-weight: 400 
		font-size: 18px 
		line-height: 24px 
		text-align: center 
		color: #FFFFFF 
		margin-top: 23px 
	&_btn 
		display: block 
		margin: 118px auto 0 
		width: 259px 
		height: 63px 
		background: linear-gradient(0deg, $main_blue_color 0%, #4CE2FF 100%) 
		box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12) 
		border-radius: 4px 
		border: none 
		font-weight: 400 
		font-size: 18px 
		text-align: center 
		text-transform: uppercase 
		color: #FFFFFF 
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21) 
		+btn_hover
.reason 
	min-height: 849px 
	padding: 18px 0 64px 
	.row 
		margin-bottom: 30px 
	.round 
		display: flex 
		align-items: center 
		justify-content: center 
		margin-left: 33px 
		width: 116px 
		height: 116px 
		border-radius: 100% 
		background: $main_blue_color 
	.item_header 
		font-weight: 400 
		font-size: 17px 
		line-height: 21px 
		color: $main_blue_color 
	.item_text 
		font-weight: 300 
		font-size: 15px 
		line-height: 21px 
		color: #202020 
	.right-col 
		padding-right: 40px 
	.left-col 
		padding-right: 10px 

.label 
	margin: 0 auto 
	font-weight: 400 
	font-size: 11px 
	text-transform: uppercase 
	color: #FFFFFF 
	width: 115px 
	height: 27px 
	background: #000000 
	text-align: center 
	padding: 5px 12px 
	transition: 0.5s all
	&:hover
		background: #FFF
		color: black

.title 
	font-weight: 700 
	font-size: 38px 
	text-align: center 
	text-transform: uppercase 
	color: #222222 
	margin: 32px 0 16px 

.subtitle
	margin-top: 19px
	font-weight: 400
	font-size: 17px
	line-height: 21px
	text-align: center

	color: #222222

.label_white
	background: white
	color: black 
	transition: 0.5s all
	&:hover
		background: #000
		color: white
.title_white
	color: #FFFFFF 
.subtitle_white
	color: #FFFFFF

.mobile
	min-height: 864px
	padding: 48px 0 79px
	background: url(../img/mobile_bg.png) center center/cover no-repeat
	.row
		margin-top: 32px
	&_item
		position: relative
		width: 320px
		height: 280px
		margin-bottom: 19px
		padding-top: 92px
		&_1
			background: url(../img/card_1.png) center center/cover no-repeat
		&_2
			background: url(../img/card_2.png) center center/cover no-repeat
		&_3
			background: url(../img/card_3.png) center center/cover no-repeat
		&_4
			background: url(../img/card_4.png) center center/cover no-repeat
		&_5
			background: url(../img/card_5.png) center center/cover no-repeat
		&_6
			background: url(../img/card_6.png) center center/cover no-repeat
		&_subtitle 
			font-weight: 400
			font-size: 24px
			line-height: 31px
			text-align: center
			text-transform: uppercase
			text-align: center
			color: #FFFFFF
		&_plus 
			display: flex
			align-items: center
			justify-content: center
			position: absolute
			bottom: 35px
			left: 50%
			transform: translateX(-50%)
			width: 26px
			height: 26px
			background-color: #000000
			margin: 0 auto
			border-radius: 100%
			padding: 6px
			cursor: pointer
			&:before
				content: ''
				display: block
				width: 14px
				height: 2px
				background-color: $main_blue_color
				transition: 0.3s all
			&:after
				content: ''
				position: absolute
				left: 50%
				transform: translateX(-50%)
				display: block
				height: 14px
				width: 2px
				background-color: $main_blue_color
				transition: 0.3s all
			&:hover
				&:before, &:after
					background-color: $add_red_color

.choice
	min-height: 898px
	position: relative
	z-index: 0
	padding-top: 57px
	background: url(../img/mobile_bg.png) center center/cover no-repeat
	.black
		position: absolute
		height: 502px
		width: 100%
		z-index: -1
		left: 0
		top: 0
		background-color: black
	&_img
		padding-top: 54px
		display: block
		margin: 0 auto
		width: 100%
	&_img_descr
		font-weight: 300
		font-size: 17px
		line-height: 24px
		text-align: center

		color: #222222

.require
	padding: 68px 0 84px
	&_block
		margin: 33px 0
		&_nmb
			margin-bottom: 0
		
	&_title 
		font-weight: 400
		font-size: 22px
		color: $main_blue_color
	&_list 
		margin-top: 22px
		list-style-type: none
		padding-left: 50px
		li 
			position: relative
			font-weight: 300
			font-size: 17px
			line-height: 24px
			color: #222222
			margin-bottom: 10px
			&:before
				position: absolute
				left: -32px
				top: 3px
				content: ''
				width: 21px
				height: 20px
				background: url('../icons/mark_blue.svg') center center/cover no-repeat
	&_descr 
		margin-top: 25px
		font-weight: 300
		font-size: 17px
		line-height: 24px
		color: $main_blue_color
	.warning
		.require_title
			color: $add_red_color
		.require_list
			li
				&:before
					background: url('../icons/mark_red.svg') center center/cover no-repeat

.city
	padding-top: 48px
	background: url(../img/world_bg.png) center (center/cover) no-repeat
	background-color: #080816
	min-height: 720px
	&_link 
		margin: 460px auto
		width: 223px
		text-align: center
		a
			font-weight: 400
			font-size: 15px
			text-transform: uppercase
			color: $main_blue_color
			transition: 0.5s all
			&:hover
				text-decoration: none
				color: $add_red_color

footer
	min-height: 156px
	position: relative
	.stripe
		width: 300px
		height: 5px
		background-color: $main_blue_color
		margin: 12px auto 35px
		transition: 0.5s all
		&:hover
			background-color: $add_red_color
	.header_links
		max-width: 350px
		display: flex
		justify-content: space-between
		margin: 0 auto 15px
		&_item 
			font-weight: 300
			font-size: 15px
			text-transform: uppercase
			color: #070716
	.subheader_links
		max-width: 390px
		display: flex
		justify-content: space-between
		margin: 0 auto 10px
		flex-wrap: wrap
		&_item 
			font-weight: 300
			font-size: 11px
			text-transform: uppercase
			color: #999999
	.language_link
		width: 57px
		margin: 0 auto
		a 
			font-weight: 400
			font-size: 11px
			text-transform: uppercase
			color: #1FBAD6
	.download_links
		position: absolute
		right: 21px
		bottom: 77px
	.social_links
		position: absolute
		left: 17px
		bottom: 72px
		display: flex
		flex-direction: row
		&_item 
			margin-left: 6px
			display: flex
			justify-content: center
			align-items: center
			background: #070716
			border-radius: 100%
			width: 32px
			height: 32px
.xs-visible
	display: none