@media (max-width: 1200px)
	.menu
		&_item 
			padding-right: 20px
		&_link
			font-size: 13px
	.promo 
		&_header 
			font-size: 50px
@media (max-width: 991px)
	.title 
		font-size: 36px
	.menu
		&_item 
			padding-right: 5px
		&_link
			font-size: 11px
	.subheader 
		&_logo 
			img 
				width: 100%
		&_official
			font-size: 11px
		&_phone 
			font-size: 19px
		&_btn 
			margin-top: 21px
			height: 40px
			width: 100%
	.promo 
		&_header 
			font-size: 40px 
		&_subheader 
			font-size: 26px
	.reason 
		.round 
			margin-left: 0
		.right-col
			padding-right: 10px
	footer 
		.social_links
			position: static
			justify-content: center
			margin-top: 15px
		.download_links 
			position: static
			text-align: center
			margin: 19px 0 30px
@media (max-width: 767px)
	.menu 
		position: fixed
		flex-direction: column
		justify-content: center
		align-items: center
		top: 0
		left: -100%
		width: 50%
		height: 100vh
		padding: 20px 15px
		background-color: rgba(0,0,0, .8)
		transition: 0.6s all
		&_active
			left: 0
		&_item 
			padding-right: 0
			border: none
			margin: 10px 0
		&_link 
			font-size: 20px
	.hamburger
		display: block
		position: absolute
		right: 10px
		top: 50%
		transform: translateY(-50%)
		width: 26px
		height: 12px
		span 
			display: block
			height: 2px
			width: 100%
			background-color: #fff
			margin-bottom: 2px
			transition: 0.5s all
		&_active
			span 
				margin-bottom: -2px
				&:nth-child(1) 
					transform: translateY(3px) rotate(-45deg)
				&:nth-child(2)
					display: none
				&:nth-child(3)
					transform: translateY(3px) rotate(45deg)
	.xs-hidden
		display: none
	.xs-visible
		display: block
	.subheader
		&_btn 
			display: block
			margin: 0 auto
			width: 200px
			height: 40px
		&_phone 
			display: block
			text-align: center
			color: #FFF
			font-size: 16px
			font-weight: 400
			text-transform: uppercase
			margin: 0 auto
			margin-top: 10px
	.promo 
		&_header 
			font-size: 40px 
			line-height: 45px
	.title 
		margin-bottom: 0
	.reason 
		padding: 22px 0 36px
		.round
			margin: 24px auto
		.item_header, .item_text
			text-align: center
			margin: 0 14px
		.row 
			margin-bottom: 0
	.mobile 
		padding-bottom: 49px
		&_item 
			margin: 0 auto 30px
@media (max-width: 575px)
	nav 
		height: 30px
	header 
		height: 30px
	.subheader
		top: 30px
		height: 60px
		&_official 
			display: none
		&_logo 
			margin-top: 18px
		&_phone 
			text-align: center
			font-size: 12px
			text-transform: uppercase
			margin-top: 11px
		&_btn 
			width: 110px
			height: 22px
			font-size: 10px
	.promo 
		min-height: 400px
		padding: 93px 0 35px 0
		&_descr 
			display: none
		&_header 
			font-size: 30px
			padding: 0 22px
			line-height: 35px
		&_subheader 
			font-size: 20px
			margin-top: 33px
			padding: 0 10px
		&_btn 
			width: 210px
			height: 50px
			margin-top: 72px
	.menu 
		width: 85%
		&_item 
			width: 100%
			text-align: center
			padding: 4px 0
		&_link 
			display: block
	.reason 
		padding: 22px 0 36px
	.title 
		font-size: 15px
		margin-top: 15px
		margin-bottom: 0
	.subtitle
		font-size: 14px
		margin-top: 0
		.round
			margin: 24px auto
		.item_header, .item_text
			text-align: center
			margin: 0 14px
		.row 
			margin-bottom: 0
	.mobile 
		padding-top: 28px
		padding-bottom: 49px
		&_item 
			width: 290px
			height: 254px
			margin: 0 auto 30px
			&_subtitle 
				margin: 0 30px
				font-size: 18px
	.choice 
		min-height: 393px
		background: #000
		padding-top: 25px
		.black
			display: none
		.title 
			display: none
		.subtitle
			display: none
		&_img_descr 
			color: #FFF
			font-size: 17px
			line-height: 24px
			padding-bottom: 31px
	.require 
		padding: 22px 0 30px
		&_title 
			font-size: 16px
			margin-top: 30px
		&_list 
			margin-top: 0
			margin-bottom: 0
			padding-left: 32px
			li 
				color: #222
				font-size: 14px
				margin-bottom: 0
		&_descr 
			font-size: 14px
			margin-top: 15px
	.city 
		padding: 24px 0 30px
		min-height: 360px
		.title 
			line-height: 16px
		.subtitle
			padding-top: 8px
		&_link 
			margin-top: 210px
			margin-bottom: 0
	footer 
		.stripe
			max-width: 255px
			margin-bottom: 20px
		.header_links
			margin: 0 auto
			padding: 0 19px
			&_item 
				font-size: 14px
		.subheader_links
			margin: 0 auto
			padding: 0 35px
			&_item 
				margin: 5px